// Josefin Sans Fonts Import
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap");

//General font styles
*,
html {
  font-family: "Josefin Sans", sans-serif;
  font-size: 0.875rem;
}
body {
  background-color: #f9f9f9;
  height: 100vh;
}

// Inputs
input {
  font-size: 11px;
  font-weight: $bold;
  border: none;
  outline: none;
}

// Checkboxes
input[type="checkbox"] {
  @include display-flex(center, center);
  appearance: none;
  margin: 0;
  width: 22px;
  height: 22px;
  border: 1.5px solid $very-light-grayish-blue;
  border-radius: 50%;
  cursor: pointer;
}
input[type="checkbox"]::after {
  font-family: "Font Awesome 5 Free";
  font-size: 11px;
  color: $very-light-gray;
  content: "\f00c";
  display: none;
}
input[type="checkbox"]:checked:after {
  display: block;
}
input:checked {
  background: linear-gradient(135deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}

// Buttons
button {
  cursor: pointer;
}

// - Visibility
// If a task is complete then apply the styles below
.complete {
  text-decoration: line-through;
  color: $light-grayish-blue;
}

// change color for filter button when clicked
.clicked {
  color: $bright-blue;
}

// Components common styles
.ToDoForm,
.ToDo,
.ToDoList__bottom-bar,
.ToDoList__Filter {
  background-color: $white;
}

// ----------------------------------------
// -------------- DARK THEME --------------
// ----------------------------------------

.dark {
  // body
  body {
    background-color: $very-dark-blue;
  }
  // Components common styles
  .ToDoForm,
  .ToDo,
  .ToDoList__bottom-bar,
  .ToDoList__Filter {
    background-color: $very-dark-desaturated-blue;
  }
  // checkboxes
  input[type="checkbox"] {
    border: 1px solid $very-dark-grayish-blue;
  }
  // complete task
  .complete {
    color: $dark-grayish-blue;
  }
}
